import React, { useLayoutEffect, useMemo } from "react"
import {
  Input,
  InputGroup,
  InputLeftElement,
  InputRightElement,
  Box,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverTrigger,
  useMediaQuery,
} from "@chakra-ui/react"

import { ChevronRightIcon, Search2Icon } from "@chakra-ui/icons"
import { graphql, navigate, useStaticQuery } from "gatsby"
import { useFlexSearch } from "react-use-flexsearch"
import useApiVersioning from "../../../hooks/useApiVersioning"

function SearchInput() {
  const [searchPlaceholderSize] = useMediaQuery("(min-width: 1024px)")
  const [searchQuery, setSearchQuery] = React.useState("")
  const [searchInputFocused, setSearchInputFocused] = React.useState(false)
  const [width, setWidth] = React.useState(0)
  const searchInputRef = React.useRef()
  const { selectedApiVersion } = useApiVersioning()

  const queryData = useStaticQuery(graphql`
    query {
      localSearchPages {
        index
        store
      }
    }
  `)

  const index = queryData.localSearchPages.index
  const store = queryData.localSearchPages.store

  const searchResults = useFlexSearch(searchQuery, index, store)

  const handleSearchQueryChange = event => {
    setSearchQuery(event.target.value)
  }

  const isOpen = useMemo(
    () => searchQuery.length > 2 && searchInputFocused,
    [searchInputFocused, searchQuery]
  )

  useLayoutEffect(() => {
    setWidth(searchInputRef?.current?.clientWidth)
  }, [])

  const navigateToSearchResult = async path => {
    try {
      await navigate(`/${path}`)
    } catch (e) {
      console.error(e)
    }
  }

  const filterSearchResults = searchResults => {
    return searchResults.filter(
      result =>
        result.version === selectedApiVersion.value &&
        result.title !== null &&
        result.title !== "" &&
        result.path !== null &&
        !result.path.toLowerCase().endsWith("/toc") &&
        result.path.toLowerCase() !== "toc"
    )
  }

  const filteredSearchResults = filterSearchResults(searchResults)

  return (
    <Popover
      isOpen={isOpen}
      initialFocusRef={searchInputRef}
      gutter={0}
      matchWidth
      margin={0}
    >
      <PopoverTrigger>
        <InputGroup>
          <InputLeftElement>
            <Search2Icon boxSize={5} />
          </InputLeftElement>
          <Input
            bg="white"
            fontSize={"1.375rem"}
            placeholder={
              searchPlaceholderSize ? "Search our documentation" : "Search"
            }
            color="rlBlack"
            minH={"2.3rem"}
            value={searchQuery}
            onChange={handleSearchQueryChange}
            onFocus={() => setSearchInputFocused(true)}
            onBlur={() => setSearchInputFocused(false)}
            ref={searchInputRef}
            borderBottomRadius={isOpen ? "none" : null}
          />
          <InputRightElement>
            <ChevronRightIcon boxSize={7} />
          </InputRightElement>
        </InputGroup>
      </PopoverTrigger>
      <PopoverContent
        borderRadius={"none"}
        borderTopWidth={3}
        borderTopColor={"rlBlack"}
        w={width}
      >
        <PopoverBody textAlign="left" maxH="30vh" overflow="scroll">
          {filteredSearchResults.length === 0 ? (
            <Box px={1} rounded="sm">
              No results found
            </Box>
          ) : (
            filteredSearchResults.map(result => (
              <Box
                key={result.id}
                onClick={() => {
                  navigateToSearchResult(result.path)
                }}
                cursor="pointer"
                px={1}
                rounded="sm"
              >
                <Box fontWeight="semibold" lineHeight="tight" noOfLines={1}>
                  {result.title}
                </Box>
                {result.description && (
                  <Box color="gray.600" fontSize="sm" noOfLines={1}>
                    {result.description}
                  </Box>
                )}
              </Box>
            ))
          )}
        </PopoverBody>
      </PopoverContent>
    </Popover>
  )
}

export default SearchInput
