import {
  Box,
  Flex,
  Stack,
  Popover,
  PopoverTrigger,
  PopoverContent,
  useDisclosure,
} from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import { TriangleDownIcon } from "@chakra-ui/icons"
import React from "react"
import DesktopSubNav from "./desktopSubNav/desktopSubNav"
import Dropdown from "../../dropdown/dropdown"

const DesktopNav = ({
  documentationData,
  selectedApiVersion,
  changeSelectedApiVersion,
  apiVersions,
}) => {
  const { isOpen, onToggle } = useDisclosure()

  return (
    <Stack direction={"row"} spacing={4} alignItems={"center"}>
      {documentationData.map(navItem => {
        return navItem.children.length > 1 ? (
          <Flex key={navItem.textElement} onClick={onToggle}>
            <Popover trigger="hover" placement="bottom-start">
              <PopoverTrigger>
                <GatsbyLink
                  to={navItem.href ?? "#"}
                  style={{
                    p: 1,
                    fontSize: "xl",
                    fontWeight: 500,
                    color: "white",
                  }}
                >
                  <Flex
                    _hover={{
                      color: "rlYellow",
                    }}
                    alignItems="center"
                    data-testid="dropdown-title"
                    fontSize={24}
                  >
                    {navItem.textElement}
                    <TriangleDownIcon
                      boxSize={3}
                      color="white"
                      ml={3}
                      transition={"all .25s ease-in-out"}
                      transform={isOpen ? "rotate(180deg)" : ""}
                    />
                  </Flex>
                </GatsbyLink>
              </PopoverTrigger>
              <PopoverContent
                boxShadow="xl"
                borderRadius={0}
                bg="white"
                p={1}
                style={{ marginTop: "0!important" }}
              >
                <Stack m={2}>
                  {navItem.children.map(child => (
                    <DesktopSubNav key={child.textElement} {...child} />
                  ))}
                </Stack>
              </PopoverContent>
            </Popover>
          </Flex>
        ) : (
          <Flex
            key={navItem.textElement}
            color="white"
            fontSize={24}
            fontWeight={500}
            mr={10}
            _hover={{
              color: "rlYellow",
            }}
          >
            <GatsbyLink to="/">Home</GatsbyLink>
          </Flex>
        )
      })}
      <Dropdown as={Box} title={selectedApiVersion?.text}>
        <Stack m={2}>
          {apiVersions?.map(version => (
            <Box
              textAlign="left"
              transition={"all .3s ease"}
              fontWeight={600}
              onClick={() => {
                changeSelectedApiVersion(version)
              }}
              key={version.value}
              display="block"
              p={1}
              _hover={{ bg: "rlYellow" }}
              cursor="pointer"
            >
              {version.text}
            </Box>
          ))}
        </Stack>
      </Dropdown>
    </Stack>
  )
}
export default DesktopNav
