import React from "react"
import { Stack } from "@chakra-ui/react"
import MobileSubNav from "./mobileSubNav/mobileSubNav"

const MobileNav = ({ documentationData }) => {
  return (
    <Stack
      bg="rlBlack20"
      p={4}
      display={{ md: "none" }}
      data-testid="mobile-title"
    >
      {documentationData.map(navItem => {
        return navItem.children.length > 1 ? (
          <MobileSubNav key={navItem.title} {...navItem} />
        ) : (
          ""
        )
      })}
    </Stack>
  )
}

export default MobileNav
