import { useEffect, useState } from "react"
import { graphql, navigate, useStaticQuery } from "gatsby"
import { globalHistory } from "@reach/router"

const useApiVersioning = () => {
  const [selectedApiVersion, setSelectedApiVersion] = useState()
  const data = useStaticQuery(graphql`
    query ApiVersionsQuery {
      allGitRemote {
        nodes {
          ref
        }
      }
      allSitePage {
        nodes {
          path
        }
      }
    }
  `)

  const currentPathname = globalHistory.location.pathname

  /**
   * the branches in the git repo. These correspond to the versions of the api
   * @type {unknown[]}
   */
  const gitBranchesNames = [...new Set(data.allGitRemote.nodes.map(v => v.ref))]

  /**
   * all the routes of the pages in the site
   */
  const routes = data.allSitePage.nodes.map(page => page.path)

  /**
   * array of objects storing the actual name of the version branch, the text that will be displayed to the user, ad the route prefix
   * @type {unknown[] | undefined}
   */
  const apiVersions = gitBranchesNames?.map(version => ({
    value: version,
    text: version === process.env.GATSBY_MAIN_BRANCH_NAME ? "latest" : version,
    routePrefix: version === process.env.GATSBY_MAIN_BRANCH_NAME ? "" : version,
  }))

  useEffect(() => {
    setSelectedApiVersion(getVersionFromUrl())
  }, [])

  const getVersionFromUrl = () => {
    let versionFromUrl = currentPathname.split("/")[1]

    //if the version extracted from the url is not found in the list of versions, use the latest version
    if (!gitBranchesNames.includes(versionFromUrl)) {
      versionFromUrl = process.env.GATSBY_MAIN_BRANCH_NAME
    }

    return apiVersions.find(v => v.value === versionFromUrl)
  }

  /**
   * switches between versions of the api
   * If it finds a page corresponding to the current one in the new version, it navigates to it, otherwise it navigates to the homepage of the new version
   * @param newVersion
   * @returns {Promise<void>}
   */
  const changeSelectedApiVersion = async newVersion => {
    const currentVersionRoutePrefix =
      selectedApiVersion.value === process.env.GATSBY_MAIN_BRANCH_NAME
        ? ""
        : selectedApiVersion.value

    const currentRoute = location.pathname ?? ""
    const routeWithoutVersionPrefix = currentRoute.replace(
      new RegExp("/^(" + currentVersionRoutePrefix + "\\.)/"),
      ""
    )

    const newRoute = `/${newVersion.routePrefix}${routeWithoutVersionPrefix}`
    setSelectedApiVersion(newVersion)

    //if there is a route to the same page on the selected version, navigate to it, else go to a fallback route on the new version
    if (routes.includes(newRoute)) {
      await navigate(`${newRoute}`)
    } else {
      await navigate(`/${newVersion.routePrefix}`)
    }
  }

  /**
   * appends to the path passed as parameter the prefix for the current version and returns it
   * @param path
   * @returns {*|string}
   */
  const getPathWithVersionPrefix = path => {
    const version = getVersionFromUrl()
    const routePrefix = version.routePrefix
    const newPath = `/${routePrefix}/${path}`

    return newPath.replace(/\/+/g, "/")
  }

  /**
   * navigates to the path passed as parameter, but within the currently selected version
   * @param to
   * @param options
   * @returns {Promise<void>}
   */
  const navigateWithinCurrentVersion = async (to, options = undefined) => {
    await navigate(getPathWithVersionPrefix(to), options)
  }

  return {
    selectedApiVersion,
    apiVersions,
    changeSelectedApiVersion,
    getPathWithVersionPrefix,
    navigateWithinCurrentVersion,
  }
}

export default useApiVersioning
